import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'
import NewRSVP from '@/components/NewRSVP'
import ViewRSVP from '@/components/ViewRSVP'
import EditRSVP from '@/components/EditRSVP'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-rsvp',
      component: NewRSVP
    },
    {
      path: '/edit/:guest_id',
      name: 'edit-rsvp',
      component: EditRSVP
    },
    {
      path: '/:guest_id',
      name: 'view-rsvp',
      component: ViewRSVP
    }
  ]
})



