<template>
  <div class="dashboard">
    <ul class="collection width-header">
      <li class="collection-header">
        <h3>RSVPs</h3>
      </li>
      <li v-for="guest in guests" v-bind:key="guest.id" class="collection-item">
        {{guest.name}}
      </li>
    </ul>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      guests: []
    }
  },
  created () {
    db.collection('guests').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'guest_id': doc.data().guest_id,
          'name': doc.data().name,
          'email': doc.data().email,
          'phone': doc.data().phone
        }
        this.guests.push(data)
      })
    })
  }
}
</script>




